*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}
body {
  color: $dark-grey;
  font-family: Helvetica, Arial, sans-serif;
  font-size: $m-size;
  line-height: 1.6;
}
.background {
  background: url("/background/img0.jpg") center center;
  //background: url("https://img.freepik.com/free-photo/purple-lines-colorful-background_23-2147746618.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: -2;
}

button {
  cursor: pointer;
}

button:disabled {
  cursor: default;
}

.is-active {
  font-weight: bold;
}
